import React from 'react';
import { Provider, themes, Flex, FlexItem, Dropdown, RadioGroup, Header, Label, Button} from '@fluentui/react-northstar';
import { CloseIcon, AcceptIcon } from '@fluentui/react-icons-northstar';

const isNilOrEmpty = str => str==null || str.length == 0

const styles={
    mainPadding:{padding:"0px 2em"},
    mainSidePadding:{padding:"10px 2em"},
    mainPdf:{display:"flex", justifyContent:"center"},
    mainTable:{
        backgroundColor: "#fff",
        border: "1px solid #d8e2f0",
        borderRadius: "4px",
        boxShadow: "0 0 10px 0 rgba(19,29,41,.05)",
        position: "relative",
        padding: "0em 3em 2em"
    },
    backButton:{
        cusor:"pointer"
    },
    label:{
        fontWeight:"bold"
    }
}

const langs={
    en:{
        heading:"Moneybird Configuration",
        apiSection:{
            title: <label style={styles.label} >API Token</label>,
            description:  <span>This is the API Token for you MoneyBird Account. You can generate one <a target="_blank" href="https://moneybird.com/user/applications/new">here</a> for personal use.</span>,
            succsess: "API Token IS vaild",
            fail: "API Token NOT vaild",
            placeholder: 'API Token',
            show:"Click to choose another api key"
        },
        contactSection:{
            title:<label style={styles.label} >Contact</label>,
            description: <span>The contact whos data will show up on the tab</span>,
            placeholder: 'Choose a Contact'
        },
        typeSection:{
            title:<label style={styles.label} >Data Type</label>,
            description: <span>The type of data to show on the tab</span>,
           
        },
        tabs:["Invoices", "Quotes"]
    },
    nl:{
        heading:"Moneybird Koppelen",
        apiSection:{
            title:<label style={styles.label} >API token invoeren</label>,
            description:  <span>Vul hier een API-token in van je Moneybird account. Je kunt <a target="_blank" href="https://moneybird.com/user/applications/new">hier</a> een API-token genereren. Vul een naam in die herkenbaar is voor deze connectie, bijvoorbeeld ‘Microsoft Teams Integratie’. Kies voor ‘API token voor persoonlijk gebruik’ en geef toegang voor alle onderdelen. Vervolgens verschijnt er een API-token in beeld. Vul deze hieronder in.</span>,
            succsess: "API-token IS geldig",
            fail: "API-token niet geldig",
            placeholder: 'API token invoeren',
            show:"Klik om een ​​andere API-sleutel te kiezen"
        },
        contactSection:{
            title:<label style={styles.label} >Contact</label>,
            description:  <span>Selecteer de Contact waarvan je de facturen wilt tonen in de Tab.</span>,
            placeholder:"Kies een Contact"
        },
        typeSection:{
            title:<label style={styles.label} >Data Type</label>,
            description: <span>Het type gegevens dat op het tabblad moet worden weergegeven</span>,
           
        },
        tabs:["Facturen", "Offertes"]
    }
}

class Config extends React.Component{
    constructor(){
        super();
        if(this.microsoftTeams)
            this.microsoftTeams.initialize();
        this.state ={
            apiKey:"",
            adminId:"",
            admins:null,
            admin:null,
            contacts:null,
            contact:null,
            loadingContacts:false,
            keys:[],
            isNew:false,
            isOpen: false,
            isContactsOpen: false,
            locale:"en-us",
            tabType:"invoices",
            showApiKeys: false
        }
        this.submit = this.submit.bind(this)
        this.getOptions = this.getOptions.bind(this)
        this.save = this.save.bind(this)
        this.getAdmin = this.getAdmin.bind(this)
        this.handleToken = this.handleToken.bind(this)
        this.handleAdmin = this.handleAdmin.bind(this)
        this.getContacts= this.getContacts.bind(this)
    }

    async getAdmin(apiKey){
        try{
            const res = await fetch(
                `https://moneybirdhandlerproxy.azurewebsites.net/api/MoneyBirdProxy`,
                {
                    mode:"cors",
                    method:"POST",
                    body: JSON.stringify({
                        method:"GET",
                        path:`administrations.json`,
                        key: apiKey
                    })
                }
            )
            const json = await res.json()
            console.log({json})
            if(json.error)
                this.setState({admins:false})
            else{
                this.setState({admin:json[0], adminId:json[0].id, admins:true})
                this.submit(json[0])
            }
        }
        catch(e){
            console.log(e)
        }
    }

    componentDidMount(){
        this.microsoftTeams = window? window.microsoftTeams: null;
        this.microsoftTeams.initialize();
        this.microsoftTeams.getContext((async context=>{
            const {tid, locale} = context;
            try{
                const res = await fetch(
                    `https://moneybirdhandlerproxy.azurewebsites.net/api/key/${tid}`,
                    {
                        mode:"cors",
                        method:"GET"
                    }
                )
                const json = await res.json()
                console.log({json})
                const keys = json.map(k=>k.key)
                const apiKey = isNilOrEmpty(keys)? "" : keys[0]
                const showApiKeys = isNilOrEmpty(apiKey)
                this.setState({keys, apiKey, showApiKeys, locale})

                if(showApiKeys == false){
                    this.getAdmin(apiKey)
                }
    
            }
            catch(e){
                console.log("BOO")
                console.log(e)
            }
    }).bind(this))
    }

    getOptions(){
        const {contacts} = this.state;
        return contacts.map(c=>({content:c.company_name, value:c.id}))
    }

    handleAdmin(e, value){
        this.setState({ admin: value.value })
        this.submit(value.value)
    }
    async getContacts(e, v){
        
        if(e){
            const {apiKey, adminId} = this.state;
            const q = e.target.value;
            try{
                this.setState({loadingContacts:true})
                const res = await fetch(
                    `https://moneybirdhandlerproxy.azurewebsites.net/api/MoneyBirdProxy`,
                    {
                        mode:"cors",
                        method:"POST",
                        body: JSON.stringify({
                            method:"GET",
                            path:`${adminId}/contacts.json${isNilOrEmpty(q)?'':"?query="+q}`,
                            key: apiKey,
                        })
                    }
                )
                const json = await res.json()
                console.log({json})
                this.setState({contacts:json, loadingContacts:false})
    
            }
            catch(e){
                console.log("BOOM!")
                console.log(e)
            }
        }
        else{
            this.setState({ contact: v.value }, this.save)
        }
    }

    handleToken(e, v){
        console.log({e, v})
        if(e){
            this.setState({apiKey:e.target.value, isOpen: true})
           //this.getAdmin(e.target.value);
        }
        else if(v){
            this.setState({apiKey:v.value, isOpen:false})
            this.getAdmin(v.value );
        }

    }
    async submit({id :adminId}){
        console.log(adminId)
        const {apiKey, isNew} = this.state;

        this.microsoftTeams.initialize();
        this.microsoftTeams.getContext(async context=>{
           const {tid} = context;
            try{
                const res = await fetch(
                    `https://moneybirdhandlerproxy.azurewebsites.net/api/MoneyBirdProxy`,
                    {
                        mode:"cors",
                        method:"POST",
                        body: JSON.stringify({
                            method:"GET",
                            path:`${adminId}/contacts.json`,
                            key: apiKey,
                           tid,
                            isNew
                        })
                    }
                )
                const json = await res.json()
                console.log({json})
                this.setState({contacts:json})
    
            }
            catch(e){
                console.log("BOOM!")
                console.log(e)
            }
        })
    }

    save(){
        
        const {apiKey, adminId, contact, locale, tabType} = this.state;
        const lang = locale.split("-")[0];

        this.microsoftTeams.initialize();
        this.microsoftTeams.settings.setValidityState(true);
        this.microsoftTeams.settings.registerOnSaveHandler((saveEvent) => {
            this.microsoftTeams.settings.setSettings({
                websiteUrl: tabType=="invoices"?`https://jp5525.github.io/teams-demo/invoices`:"https://jp5525.github.io/teams-demo/quotes",
                contentUrl: tabType=="invoices"?`https://jp5525.github.io/teams-demo/invoices`:"https://jp5525.github.io/teams-demo/quotes",
                entityId: JSON.stringify({apiKey, adminId, contact}),
                suggestedDisplayName: `Moneybird ${tabType=="invoices"?langs[lang].tabs[0]:langs[lang].tabs[1]}`
            });
            saveEvent.notifySuccess();
        });
    }

    render(){
        const {contacts, contact, admins, admin, apiKey, keys, isOpen, locale, loadingContacts, showApiKeys } = this.state;
        const lang = locale? langs[locale.split("-")[0]]: null
        console.log("=====================")
        console.log(this.state)
        const radioOptions = lang? [
            {
              key: 'invoices',
              value: 'invoices',
              name: 'invoices',
              label: lang.tabs[0],
              onClick:()=>this.setState({tabType:"invoices"})
            },
            {
              key: 'quotes',
              name: 'quotes',
              value: 'quotes',
              label: lang.tabs[1],
              onClick:()=>this.setState({tabType:"quotes"})
            }
          ]: null

        return (
            lang?
                <Provider theme={themes.teams}>
                    <div style={styles.mainPadding}>
                        <div style={styles.mainTable}>
                            <Header content={lang.heading}/>
                            <Flex gap="gap.small" vAlign="center" hAlign="start">
                                <FlexItem justifyContent="center">
                                    <form onSubmit={this.submit} style={{width:"100%"}}>
                                        
                                        {lang.apiSection.title} <br/>
                                        {lang.apiSection.description}
                                        {admins== false? <> <br/><Label content={lang.apiSection.fail} icon={{as: CloseIcon}}/></> : null}
                                        {admins? <> <br/><Label content={lang.apiSection.succsess} icon={{as: AcceptIcon}}/></> : null}
                                        {showApiKeys?<Dropdown
                                            items={keys}
                                            placeholder={lang.apiSection.placeholder}
                                            onChange={this.handleToken}
                                            name="apiKey"
                                            id="apiKey"
                                            clearable={apiKey && apiKey.length>0}
                                            fluid={true}
                                            search={true}
                                            open={isOpen}
                                            noResultsMessage={{
                                                content:`Add... ${apiKey}`, 
                                                onClick: m=>{
                                                    this.setState({isNew:true, keys:[apiKey, ...keys], isOpen:false}, ()=>this.getAdmin(apiKey))
                                                    
                                                }, 
                                                style:{cursor: "pointer"}
                                            }}
                                        />
                                        :
                                        <div><Button text content={lang.apiSection.show} onClick={()=>this.setState({showApiKeys: true})}/></div>
                                        }
                                        
                                       {/* <br />

                                        {lang.adminSection.title} <br/>
                                        {lang.adminSection.description}
                                        <Dropdown
                                            items={admins? admins.map(a=>({content:a.name, value:a.id})): []}
                                            value={admin? admin.content : null}
                                            placeholder={lang.adminSection.placeholder}
                                            onChange={this.handleAdmin}
                                            name="Admin"
                                            disabled={admins? false: true}
                                            fluid={true}
                                       />*/}

                                        <br />

                                        {lang.contactSection.title} <br/>
                                        {lang.contactSection.description}
                                        <Dropdown
                                            ref={r=>this.drop=r}
                                            items={contacts? this.getOptions(): []}
                                            value={contact? contact.content : null}
                                            placeholder={lang.contactSection.placeholder}
                                            search={true}
                                            loading={loadingContacts}
                                            loadingMessage="Loading..."
                                            onChange={this.getContacts}
                                            name="contact"
                                            disabled={contacts? false: true}
                                            itemToString={i=>i?i.content:''}
                                            fluid={true}
                                            />

                                        <br />

                                        {lang.typeSection.title} <br/>
                                        {lang.typeSection.description}
                                        <RadioGroup
                                            defaultCheckedValue="invoices"
                                            items={radioOptions}
                                            onCheckedValueChange={(e, props) =>{
                                                console.log(props);
                                                this.setState({tabType: props.value})
                                            }}
                                            style={{margingTop:"5px"}}
                                            />

                                    </form>
                                </FlexItem>
                            </Flex>
                        </div>
                    </div>
                
                </Provider>:
                null
            
            
        )

    }
}

export default Config;
